* {
    font-family: sans-serif;
}

header *,
dialog * {
    font-size: min(3vw, 3vh, 20px);
}

html,
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 100%;
    width: 100%;
}

body {
    background: linear-gradient(0.25turn, #BBCCEE, #CCEEFF, #CCDDAA, #EEEEBB, #FFCCCC);
}

header {
    height: 2em;
    width: 100%;
    background: #FFF8;
    display: flex;
    gap: 1em;
    justify-content: center;
    align-items: center;
}

#svg-puzzle-container,
#svg-puzzle {
    height: 100%;
    width: 100%;
    max-width: 100vw;
    max-height: calc(100vh - 2em);
}

#svg-puzzle>path {
    stroke-width: 0.025;
}

#svg-puzzle>path.dragging {
    filter: contrast(1.5);
}

#svg-puzzle>path.shadowGoal {
    fill-opacity: 0.25;
    stroke-opacity: 0.5;
}

#change-puzzle-dialog[open] {
    display: flex;
    flex-direction: column;
    gap: 1em;
    width: 20em;
    align-items: center;
    border-radius: 0.5em;
}

#puzzle-dialog-buttons {
    display: flex;
    flex-direction: row;
    gap: 1em;
}

#error-msg {
    color: darkred;
}

.puzzle-string {
    font-family: monospace;
